import React from 'react';
import './LsideMenuStyle.css';

const PhpList = (props) => {
    function send(page2) {
        props.onChangePage(page2)
    }

    return (
        <div className="phplist">
            <button onClick={() => { send('php') }}> PHP   </button>
        </div>
    );
}

export default PhpList;