import React from 'react';
import './LsideMenuStyle.css';

const RefBlogList = (props) => {
    function send(page2) {
        props.onChangePage(page2)
    }

    return (
        <div className="refbloglist">
            <button onClick={() => { send('refblog') }}>   </button>
        </div>
    );
}

export default RefBlogList;