import React from 'react';
import './LsideMenuStyle.css';

const KeyMapList = (props) => {
    function send(page2) {
        props.onChangePage(page2)
    }

    return (
        <div className="menucontainer">
            <div className="menu menu1">
                <button onClick={() => { send('intellijkeymap') }}> Intellj    </button>
                <button onClick={() => { send('eclipsekeymap') }}> Eclipse    </button>
                <button onClick={() => { send('browserkeymap') }}>     </button>
            </div>
            <div className="menu menu2">
                <button onClick={() => { send('vscodekeymap') }}> VSCode    </button>
                <button onClick={() => { send('windowkeymap') }}> Window    </button>
            </div>
        </div>
    );
}

export default KeyMapList;