import React from 'react';
import './LsideMenuStyle.css';

const DatabaseList = (props) => {
    function send(page2) {
        props.onChangePage(page2)
    }

    return (
        <div className="databaselist">
            <button onClick={() => { send('oracledbstudy') }}> OreacleDB  </button>
            <button onClick={() => { send('mysqlstudy') }}> Mysql  </button>
            <button onClick={() => { send('databasename') }}> Database   </button>
        </div>
    );
}

export default DatabaseList;