import React, { useEffect, useRef, useState } from 'react';

const NetworkStudy = (props) => {

    const [scrollY, setScrollY] = useState(0);
    function logit() {
        setScrollY(window.pageYOffset);


        // 
        let elementHeight1 = document.getElementsByClassName('navMenu')[0].scrollHeight;
        let elementHeight2 = document.getElementsByClassName('navMenu')[0].scrollHeight +
            document.getElementsByClassName('navMenu2')[0].scrollHeight + 26;
        let testLeft = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left;
        let testdis = document.getElementsByClassName('common_style')[0].getBoundingClientRect().left
            - document.getElementsByClassName('lblocknav_container')[0].getBoundingClientRect().left;
        // console.log(window.outerHeight);


        //    +  
        if (window.innerWidth < 1024) {
            blockRef.current[0].style.position = 'fixed';
            document.documentElement.scrollTop < elementHeight1 ?
                blockRef.current[0].style.top = elementHeight1 + "px" :
                blockRef.current[0].style.top = "10px";
            blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
            blockRef.current[0].style.maxWidth = '420px';
            blockRef.current[1].style.left = '25%';
            blockRef.current[0].style.height = window.outerHeight;
            blockRef.current[0].style.display === 'inline-block' ?
                document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none' :
                document.getElementsByClassName('lblocknav_btn')[0].style.display = 'inline-block';
        }
        //   
        else if (document.documentElement.scrollTop < elementHeight2 && window.innerWidth > 1023) {
            //    ,  ,  
            blockRef.current[0].style.position = 'fixed';
            blockRef.current[0].style.top = elementHeight2 + "px";
            blockRef.current[0].style.display = 'inline-block';
            blockRef.current[0].style.maxWidth = testdis + "px";
            blockRef.current[0].style.height = window.outerHeight;
            blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
            //       
            blockRef.current[1].style.left = '25%';
            document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
        }
        else if (document.documentElement.scrollTop > elementHeight2 && window.innerWidth > 1023) {
            //    ,  ,  
            blockRef.current[0].style.position = 'fixed';
            blockRef.current[0].style.top = "10px";
            blockRef.current[0].style.display = 'inline-block';
            blockRef.current[0].style.maxWidth = testdis + "px";
            blockRef.current[0].style.height = window.outerHeight;
            blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
            //       
            blockRef.current[1].style.left = '25%';
            document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
        }
    }

    const blockRef = useRef([]);

    useEffect(() => {
        function watchScroll() {
            window.addEventListener("scroll", logit);
        }
        watchScroll();
        return () => {
            window.removeEventListener("scroll", logit);
        };
    });

    return (
        <>
            <button className="lblocknav_btn" onClick={() => {
                blockRef.current[0].style.display === 'inline-block' ?
                    blockRef.current[0].style.display = 'none' :
                    blockRef.current[0].style.display = 'inline-block';
                document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
            }}>  </button>
            <div className="lblocknav_container" ref={(elem) => (blockRef.current[0] = elem)}>
                <div className="lblocknav">
                    <span className="mtitle">
                        <a href="#main" className="col_b"> <b>    </b> </a>
                        <button className="lblocknav_btn2" onClick={() => {
                            document.getElementsByClassName('lblocknav_btn')[0].style.display = 'block';
                            document.getElementsByClassName('lblocknav_container')[0].style.display = 'none';
                        }}>  </button>
                    </span>
                    <div className="hyperlink">
                        <details open>
                            <summary>  </summary>
                            <details open>
                                <summary className="col_g">   </summary>
                                <a href="#?" className="col_p"> ? </a>
                                <a href="#MAC, IP, PORT" className="col_p"> MAC, IP, PORT </a>
                                <a href="# " className="col_p">   </a>
                                <a href="#?" className="col_p"> ? </a>
                                <a href="#WWW?" className="col_p"> WWW? </a>
                                <a href="#(protocol)?" className="col_p"> (protocol)? </a>
                                <a href="#TCP/IP " className="col_p"> TCP/IP  </a>
                                <a href="#4Way Handshake" className="col_p"> 4Way Handshake </a>
                                <a href="#UDP/IP " className="col_p"> UDP/IP  </a>
                                <a href="#RIP " className="col_p"> RIP  </a>
                                <a href="#" className="col_p">  </a>
                                <a href="# " className="col_p">   </a>
                                {/* <a href="#" className="col_p"> </a> */}
                            </details>
                        </details>
                    </div>
                </div>
            </div>
            <div className="common_style" ref={(elem) => (blockRef.current[1] = elem)} >
                <span className="lblock">
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  ? <a name="?" style={{ visibility: "hidden" }}> ? </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">  : (  ,)    DATA   ,  </span>
                                <span className="mblock">
                                    <li> , ,  PC 1  ,     </li>
                                </span>
                                {/*  */}
                                <span className="sstitle">  () </span>
                                <span className="mblock">
                                    <li> PAN(Personal Area Network) :     # , IEE 802.15 </li>
                                    <li> LAN(Local Area Network) :   ,  , IEE 802.11 #(), ,     </li>
                                    <li> MAN(Metropolitan Area Network) :    , IEE 802.16  </li>
                                    <li> WAN(Widw Area Network) :   , 802.20 # , , ,   </li>
                                    <li> VLAN  </li>
                                    <li> CAN </li>
                                    <li> PAN </li>
                                    <li>  </li>
                                </span>
                                {/*  */}
                                <span className="sstitle"> LAN  () , (Topology) </span>
                                <small>        </small>
                                <span className="mblock">
                                    <li className="sstitle"> Mesh :     </li>
                                    <li className="sstitle">  :        </li>
                                    <li className="sstitle"> Bus :      ,   Terminator     </li>
                                    <span className="sblock">
                                        <li className="tab_4 col_be">   ,  ,   </li>
                                        <li className="tab_4 col_r">     ,    ,    </li>
                                    </span>
                                    <li className="sstitle"> Ring :    </li>
                                    <span className="sblock">
                                        <li className="tab_4 col_be">   ,  </li>
                                        <li className="tab_4 col_r">     ,  ,  </li>
                                    </span>
                                    <li className="sstitle"> Star :        </li>
                                    <span className="sblock">
                                        <li className="tab_4 col_be">    ,    ,      #   </li>
                                        <li className="tab_4 col_r">         ,      </li>
                                    </span>
                                    {/* <li>  :  </li>
                                    <li className="tab_4 col_be">  </li>
                                    <li className="tab_4 col_r">  </li> */}
                                </span>
                                {/*  */}
                                <span className="sstitle">    </span>
                                <span className="mblock">
                                    <li> 1:1( ) : # 1:1  </li>
                                    <li> 1:N( ) :           ( ex) IPTV  ) </li>
                                    <li> 1:( ) :            ,   </li>
                                </span>
                                {/*  */}
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  MAC, IP, PORT <a name="MAC, IP, PORT?" style={{ visibility: "hidden" }}> MAC, IP, PORT </a> </summary>
                            <span className="sblock">
                                <span className="sstitle"> MAC </span>
                                <span className="mblock">
                                    <li>        
                                        (0001) , (0002)     .    
                                         MAC  . <br />  MAC fff.fff.fff.fff   . </li>
                                </span>
                                {/*  */}
                                <span className="sstitle"> IP </span>
                                <span className="mblock">
                                    <li> MAC  IP  . IP      IP  .
                                        <br /> IP 255.255.255.255   . </li>
                                </span>
                                {/*  */}
                                <span className="sstitle"> PORT </span>
                                <span className="mblock">
                                    <li> PORT ,    (,, )    .
                                        <br /> 0~65536  0~1023      . ex) 80   . </li>
                                </span>
                                {/*  */}
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                                {/*  */}
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">    <a name=" " style={{ visibility: "hidden" }}>   </a> </summary>
                            <span className="sblock">
                                <span className="sstitle"> DNS  </span>
                                <span className="mblock">
                                    <li>  (www.naver.com) IP(111.111.111.111)   , UDP   </li>
                                </span>
                                {/*  */}
                                <span className="sstitle"> TFTP  </span>
                                <span className="mblock">
                                    <li>    </li>
                                </span>
                                {/*  */}
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                                {/*  */}
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  ? <a name="?" style={{ visibility: "hidden" }}> ? </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">       ,       </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  WWW? <a name="WWW?" style={{ visibility: "hidden" }}> WWW? </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">        </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}

                    <span className="mblock">
                        <details>
                            <summary className="stitle">  (protocol)? <a name="(protocol)?" style={{ visibility: "hidden" }}> (protocol)? </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">     () #      </span>
                                <span className="sstitle">    </span>
                                <span className="mblock">
                                    <li>   , , , , , , , , ,  </li>
                                    <li>  :         </li>
                                    <li>  :         </li>
                                </span>
                                {/*  */}
                                <span className="sstitle">   </span>
                                <span className="mblock">
                                    <li>   : Ethernet #MAC  </li>
                                    <li>   : ICMP , IPv4 , IPv6 , ARP #IP </li>
                                    <li>    : TCP , UDP # </li>
                                </span>
                                {/*  */}
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  TCP/IP  <a name="TCP/IP " style={{ visibility: "hidden" }}> TCP/IP  </a> </summary>
                            <span className="sblock">
                                <span className="sstitle"> TCP/IP :        , IP   , TCP  </span>
                                <span className="mblock">
                                    <li> 1. 3Way Handshake  </li>
                                    <li> 2. </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">

                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  4Way Handshake <a name="4Way Handshake" style={{ visibility: "hidden" }}> 4Way Handshake </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">      </span>
                                <small> 3Way Handshake    ? </small>
                                <span className="mblock">
                                    <li> 1.    <br />   ESTABLISHE  , FIN  ,  FIN-WAIT1  </li>
                                    <li> 2.    <br /> ACK  ,       TIME_WAIT  </li>
                                    <li> 2-1.   <br />  FIN-WAIT2  </li>
                                    <li> 3.    <br />    FIN   ,  LAST-ACK  </li> <br />
                                    <li> 4.    <br />  TIME-WAIT  , ACK   </li>
                                    <li> 5.    <br /> CLOSED   </li> <br />
                                    <li>  TIME-WAIT  :         (default 240second)       </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  UDP/IP  <a name="UDP/IP " style={{ visibility: "hidden" }}> UDP/IP </a> </summary>
                            <span className="sblock">
                                <span className="sstitle"> UDP/IP :         , IP   , UDP  </span>
                                <span className="sstitle">   (User Datagram Protocol, UDP) </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                                <span className="sstitle"> UDP   </span>
                                <span className="mblock">
                                    <li> | Source Port = 2Byte | Destination Port = 2Byte | # ,   </li>
                                    <li> | Length = 2Byte | Checksum = 2Byte | #(+) ,      </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  RIP  <a name="RIP " style={{ visibility: "hidden" }}> RIP  </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">      </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">    <a name=" " style={{ visibility: "hidden" }}>   </a> </summary>
                            <span className="sblock">
                                <span className="sstitle"> TP(Twisted Pair Cable) </span>
                                <small>    </small>
                                <span className="mblock">
                                    <li> STP(Shielded twisted pair) </li>
                                    <li> UTP(Unshielded twisted pair) </li>
                                </span>
                                {/*  */}
                                <span className="sstitle"> Coaxial Cable </span>
                                <small> , BNC , TP Cable    </small>
                                <span className="mblock">
                                    <li>        #  TV,   </li>
                                </span>
                                {/*  */}
                                <span className="sstitle"> Optical Fiber </span>
                                <small>  ,  ,    </small>
                                <span className="mblock">
                                    <li className="color_be">  </li>
                                    <li className="color_r">  ,   ,    </li>
                                </span>
                                {/*  */}
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                </span>
            </div>
        </>
    );
}

export default NetworkStudy;