import React, { useEffect, useRef } from 'react';

const LinuxShellScript = (props) => {

  function logit() {

    // 
    let elementHeight1 = document.getElementsByClassName('navMenu')[0].scrollHeight;
    let elementHeight2 = document.getElementsByClassName('navMenu')[0].scrollHeight +
      document.getElementsByClassName('navMenu2')[0].scrollHeight + 26;
    let testdis = document.getElementsByClassName('common_style')[0].getBoundingClientRect().left
      - document.getElementsByClassName('lblocknav_container')[0].getBoundingClientRect().left;
    // console.log(window.outerHeight);


    //    +  
    if (window.innerWidth < 1024) {
      blockRef.current[0].style.position = 'fixed';
      document.documentElement.scrollTop < elementHeight1 ?
        blockRef.current[0].style.top = elementHeight1 + "px" :
        blockRef.current[0].style.top = "10px";
      blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
      blockRef.current[0].style.maxWidth = '420px';
      blockRef.current[1].style.left = '25%';
      blockRef.current[0].style.height = window.outerHeight;
      blockRef.current[0].style.display === 'inline-block' ?
        document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none' :
        document.getElementsByClassName('lblocknav_btn')[0].style.display = 'inline-block';
    }
    //   
    else if (document.documentElement.scrollTop < elementHeight2 && window.innerWidth > 1023) {
      //    ,  ,  
      blockRef.current[0].style.position = 'fixed';
      blockRef.current[0].style.top = elementHeight2 + "px";
      blockRef.current[0].style.display = 'inline-block';
      blockRef.current[0].style.maxWidth = testdis + "px";
      blockRef.current[0].style.height = window.outerHeight;
      blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
      //       
      blockRef.current[1].style.left = '25%';
      document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
    }
    else if (document.documentElement.scrollTop > elementHeight2 && window.innerWidth > 1023) {
      //    ,  ,  
      blockRef.current[0].style.position = 'fixed';
      blockRef.current[0].style.top = "10px";
      blockRef.current[0].style.display = 'inline-block';
      blockRef.current[0].style.maxWidth = testdis + "px";
      blockRef.current[0].style.height = window.outerHeight;
      blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
      //       
      blockRef.current[1].style.left = '25%';
      document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
    }
  }

  const blockRef = useRef([]);

  useEffect(() => {
    function watchScroll() {
      window.addEventListener("scroll", logit);
    }
    watchScroll();
    return () => {
      window.removeEventListener("scroll", logit);
    };
  });

  return (
    <>
      <button className="lblocknav_btn" onClick={() => {
        blockRef.current[0].style.display === 'inline-block' ?
          blockRef.current[0].style.display = 'none' :
          blockRef.current[0].style.display = 'inline-block';
        document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
      }}>  </button>
      <div className="lblocknav_container" ref={(elem) => (blockRef.current[0] = elem)}>
        <div className="lblocknav">
          <span className="mtitle">
            <a href="#main" className="col_b"> <b>    </b> </a>
            <button className="lblocknav_btn2" onClick={() => {
              document.getElementsByClassName('lblocknav_btn')[0].style.display = 'block';
              document.getElementsByClassName('lblocknav_container')[0].style.display = 'none';
            }}>  </button>
          </span>
          <div className="hyperlink">
            <details open>
              <summary>  </summary>
              <details open>
                <summary className="col_g">   </summary>
                <a href="#" className="col_p">  </a>
                {/* <a href="#" className="col_p"> </a> */}
              </details>
            </details>
          </div>
        </div>
      </div>
      <div className="common_style" ref={(elem) => (blockRef.current[1] = elem)} >
        <span className="lblock">
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">      <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
              <span className="sblock">
                {/*  */}
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li> # </li>
                </span>
                {/*  */}
                <span className="sstitle"> #!(Shebang) </span>
                <span className="mblock">
                  <li>    </li>
                  <li>   
                    <li> #!/bin/sh (Bourne Shell) </li>
                    <li> #!/bin/csh (C Shell) </li>
                    <li> #!/bin/ksh (Korn Shell) </li>
                    <li> #!/bin/bash (Bash Shell) </li>
                    <li> #!/usr/bin/perl (Perl)</li>
                    <li> #!/usr/bin/python (Python) </li>
                  </li>
                  <li>  </li>
                </span>
                {/*  */}
                <span className="sstitle"> () </span>
                <span className="mblock">
                  <li>       . </li>
                </span>
                {/*  */}
                <span className="sstitle">     </span>
                <span className="mblock">
                  <li>        () 
                          </li>
                </span>
                {/*  */}
                <span className="sstitle">     </span>
                <span className="mblock">
                  <li> 1. vi .sh    </li>
                  <li> 2. #!/bin/bash ,   (    .) </li>
                  <li> 3.   </li>
                  <li> 4.        chmod   . </li>
                  <li> 5. ./.sh      , bash sh .sh    </li>
                </span>
                {/*  */}
                <span className="sstitle">      </span>
                <span className="mblock">
                  <li> read             . </li>
                </span>
                {/*  */}
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
                {/*  */}
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">   <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
              <span className="sblock">
                <span className="sstitle">   </span>
                <span className="mblock">
                  <li> ="" , #   ,     </li>
                  <li> = , #   ,     </li>
                </span>
                {/*  */}
                <span className="sstitle">   </span>
                <span className="mblock">
                  <li> ${'{}'} :   </li>
                  <li> ${'{^}'} :      </li>
                  <li> ${'{^^}'} :     </li>
                  <li> ${'{,}'} :      </li>
                  <li> ${'{,,}'} :     </li>
                </span>
                {/*  */}
                <span className="sstitle">       </span>
                <span className="mblock">
                  <li> export ="" </li>
                  <li> ./@.sh , #    </li>
                </span>
                {/*  */}
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">   <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
              <span className="sblock">
                <span className="sstitle">   </span>
                <span className="mblock">
                  <li> ${0} , ${1} , ${2} :     , 0   </li>
                  <li> ${'{#}'} ,  </li>
                  <li> ${'{*}'} ,    </li>
                  <li> ${'{@}'} ,   </li>
                  <li> ${'{:start:count}'} :     </li>
                  <li> ${'{[index]}'} :     </li>
                  <li> ${'{$!}'} ,  </li>
                  <li> ${'{$$}'} ,   PID </li>
                  <li> ${'{$?}'} ,    </li>
                  <li> ${'{$_}'} ,        </li>
                  <li> ${'{$-}'} ,  Shell     </li>
                  <li> ${'{#}'} :    </li>
                  <li> ${'{#[*]}'} :    </li>
                  <li> ${'{:start:count}'} :     </li>
                  <li> ${'{:=""'} #      </li>
                  <li> ${'{+""'} #       </li>
                  <li> ${'{-""'} #         . </li>
                  <li> ${'{##}'} :       </li>
                  <li> ${'{###}'} :       </li>
                  <li> ${'{#%}'} :        </li>
                  <li> ${'{#%%}'} :      </li>
                  <li> ${'{#//}'} :      </li>
                  <li> ${'{#///}'} :      </li>
                  <li> ${'{#/#/}'} :         </li>
                  <li> ${'{#/%/}'} :         </li>
                </span>
                {/*  */}
                <span className="sstitle">     </span>
                <span className="mblock">
                  <li> ./@.sh 1 2 3 </li>
                </span>
                {/*  */}
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">    <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
              <span className="sblock">
                <span className="sstitle">    </span>
                <span className="mblock">
                  <li> HOME :    </li>
                  <li> PATH :    </li>
                  <li> LANG :    </li>
                  <li> UID :  UID </li>
                  <li> SHELL :    </li>
                  <li> USER :   </li>
                  <li> FUNCNAME :      </li>
                  <li> TERM :   </li>
                </span>
                {/*  */}
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">   <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
              <span className="sblock">
                <span className="sstitle"> if   </span>
                <span className="mblock">
                  <li> if   {'&&'}  {'||'}  ; then </li>
                  <li>  </li>
                  <li> elif [] ; then </li>
                  <li>  </li>
                  <li> else </li>
                  <li>  </li>
                  <li> fi </li>
                </span>
                {/*  */}
                <span className="sstitle"> else   </span>
                <span className="mblock">
                  <li> case ${'{1}'} in </li>
                  <li> 1 | 2)  ;; </li>
                  <li> [fF]*{')'}  ;; </li>
                  <li> *{')'}  ;; </li>
                  <li> esac </li>
                </span>
                {/*  */}
                <span className="sstitle">   </span>
                <span className="mblock">
                  <li> -a : AND </li>
                  <li> -o : OR </li>
                </span>
                {/*  */}
                <span className="sstitle">   , []  </span>
                <span className="mblock">
                  <li> if [ ${'{1}'} -eq  ${'{2}'} ]; , # equal(=)</li>
                  <li> if [ ${'{1}'} -ne  ${'{2}'} ]; , # not equal(!=)</li>
                  <li> if [ ${'{1}'} -gt  ${'{2}'} ]; , # greater(&gt;) </li>
                  <li> if [ ${'{1}'} -ge  ${'{2}'} ]; , # greater or equal(&lt;)</li>
                  <li> if [ ${'{1}'} -lt  ${'{2}'} ]; , # little(&lt;)</li>
                  <li> if [ ${'{1}'} -le  ${'{2}'} ]; , # little or equal(&lt;=)</li>
                  <li> if [ -n   ]; , #   0  </li>
                  <li> if [ -z   ]; , #   0  </li>
                </span>
                {/*  */}
                <span className="sstitle">     . []  </span>
                <span className="mblock">
                  <li> if [  -d $() ]; , #    </li>
                  <li> if [ ! -d $()];  , #     </li>
                  <li> if [  -e $() ]; , #    </li>
                  <li> if [ ! -e $()];  , #     </li>
                  <li> if [  -L $() ]; , #     </li>
                  <li> if [  -S $() ]; , #    </li>
                  <li> if [  -s $() ]; , #   0   </li>
                  <li> if [  -r $() ]; , #      </li>
                  <li> if [  -w $() ]; , #      </li>
                  <li> if [  -x $() ]; , #       </li>
                  <li> if [  -f $() ]; , #     </li>
                  <li> if [  -c $() ]; , #     </li>
                  <li> if [  -g $() ]; , #  set-user-id   </li>
                  <li> if [ $(1) -nt $(2) ]; , # 1    </li>
                  <li> if [ $(1) -os $(2) ]; , # 1    </li>
                  <li> if [ $(1) -ef $(2) ]; , # 1,2   </li>
                </span>
                {/*  */}
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">   <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
              <span className="sblock">
                <span className="sstitle"> for   </span>
                <span className="mblock">
                  <li> for ; </li>
                  <li> do </li>
                  <li>  </li>
                  <li> done </li>
                </span>
                {/*  */}
                <span className="sstitle"> while   (  until  .) </span>
                <span className="mblock">
                  <li> while (()); , while [ true ] , while [ $i -le 10 ] </li>
                  <li> do</li>
                  <li> </li>
                  <li> ((   ))</li>
                  <li> `expr $i + 1`  , # backtick, backquoto , grave accent,  </li>
                  <li> done </li>
                </span>
                {/*  */}
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li> for i in 1 2 3 4 5 </li>
                  <li> for i in ${'{list}'} , list = "1 2 3 4 5" </li>
                  <li> for i in {'{1..5}'} . # 1~5 </li>
                  <li> for i in {'{1..5..2}'} , # 1,3,5 </li>
                  <li> for i in `seq 1 2 10` , # 1,3,5,7,9 </li>
                  <li> for i in `seq 10 -2 1` , # 10,8,6,4,2 </li>
                  <li> for i in ${'{arr[@]}'} , arr = (1 2 3 4 5) </li>
                  <li> for ((i=0; i&gt;5; i++)); </li>
                  <li> for i in * .*  , #     </li>
                  <li> for i in f*  , # f   </li>
                </span>
                {/*  */}
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">   <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
              <span className="sblock">
                <span className="sstitle">     </span>
                <span className="mblock">
                  <li>  = (1 2 3)  </li>
                  <li> 1.   </li>
                  <li> 2. 1   </li>
                </span>
                {/*  */}
                <span className="sstitle">    </span>
                <span className="mblock">
                  <li> [] =   </li>
                </span>
                {/*  */}
                <span className="sstitle">    </span>
                <span className="mblock">
                  <li> unset []  </li>
                </span>
                {/*  */}
                <span className="sstitle">   </span>
                <span className="mblock">
                  <li> ${'{[@]}'} , #    </li>
                  <li> ${'{[]}'} </li>
                </span>
                {/*  */}
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">   <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
              <span className="sblock">
                <span className="sstitle">   </span>
                <span className="mblock">
                  <li> function (){'{'} </li>
                  <li>  </li>
                  <li> {'}'} </li>
                </span>
                {/*  */}
                <span className="sstitle">   </span>
                <span className="mblock">
                  <li>    , #    </li>
                </span>
                {/*  */}
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">    <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
              <span className="sblock">
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li> sleep () </li>
                </span>
                {/*  */}
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
                {/*  */}
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">   <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
              <span className="sblock">
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
                {/*  */}
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
                {/*  */}
              </span>
            </details>
          </span>
          {/*  */}
        </span>
      </div>
    </>
  );
}

export default LinuxShellScript;