import React from 'react';
import './LsideMenuStyle.css';

const LinuxList = (props) => {
    function send(page2) {
        props.onChangePage(page2)
    }

    return (
        <div className="menucontainer">
            <div className="menu menu1">
                <button onClick={() => { send('linuxstudy') }}>   </button>
                <button onClick={() => { send('linuxcommand') }}>   </button>
                <button onClick={() => { send('linuxdirectory') }}>    </button>
                <button onClick={() => { send('linuxshellscript') }}>   </button>
            </div>
            <div className="menu menu2">
                <button onClick={() => { send('linuxubuntu') }}>   </button>
                <button onClick={() => { send('linuxcentos7') }}>  CentOS7 </button>
                <button onClick={() => { send('mobaxterm') }}> MobaXterm </button>
                <button onClick={() => { send('linuxvimeditor') }}> VimEditor </button>
                <button onClick={() => { send('linuxshell') }}>   </button>
            </div>
        </div>
    );
}

export default LinuxList;