import React, { useEffect, useRef, useState } from 'react';

const BackendBrowserDevTool = (props) => {

  const [scrollY, setScrollY] = useState(0);
  function logit() {
    setScrollY(window.pageYOffset);


    // 
    let elementHeight1 = document.getElementsByClassName('navMenu')[0].scrollHeight;
    let elementHeight2 = document.getElementsByClassName('navMenu')[0].scrollHeight +
      document.getElementsByClassName('navMenu2')[0].scrollHeight + 26;
    let testLeft = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left;
    let testdis = document.getElementsByClassName('common_style')[0].getBoundingClientRect().left
      - document.getElementsByClassName('lblocknav_container')[0].getBoundingClientRect().left;
    // console.log(window.outerHeight);


    //    +  
    if (window.innerWidth < 1024) {
      blockRef.current[0].style.position = 'fixed';
      document.documentElement.scrollTop < elementHeight1 ?
        blockRef.current[0].style.top = elementHeight1 + "px" :
        blockRef.current[0].style.top = "10px";
      blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
      blockRef.current[0].style.maxWidth = '420px';
      blockRef.current[1].style.left = '25%';
      blockRef.current[0].style.height = window.outerHeight;
      blockRef.current[0].style.display === 'inline-block' ?
        document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none' :
        document.getElementsByClassName('lblocknav_btn')[0].style.display = 'inline-block';
    }
    //   
    else if (document.documentElement.scrollTop < elementHeight2 && window.innerWidth > 1023) {
      //    ,  ,  
      blockRef.current[0].style.position = 'fixed';
      blockRef.current[0].style.top = elementHeight2 + "px";
      blockRef.current[0].style.display = 'inline-block';
      blockRef.current[0].style.maxWidth = testdis + "px";
      blockRef.current[0].style.height = window.outerHeight;
      blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
      //       
      blockRef.current[1].style.left = '25%';
      document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
    }
    else if (document.documentElement.scrollTop > elementHeight2 && window.innerWidth > 1023) {
      //    ,  ,  
      blockRef.current[0].style.position = 'fixed';
      blockRef.current[0].style.top = "10px";
      blockRef.current[0].style.display = 'inline-block';
      blockRef.current[0].style.maxWidth = testdis + "px";
      blockRef.current[0].style.height = window.outerHeight;
      blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
      //       
      blockRef.current[1].style.left = '25%';
      document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
    }
  }

  const blockRef = useRef([]);

  useEffect(() => {
    function watchScroll() {
      window.addEventListener("scroll", logit);
    }
    watchScroll();
    return () => {
      window.removeEventListener("scroll", logit);
    };
  });

  return (
    <>
      <button className="lblocknav_btn" onClick={() => {
        blockRef.current[0].style.display === 'inline-block' ?
          blockRef.current[0].style.display = 'none' :
          blockRef.current[0].style.display = 'inline-block';
        document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
      }}>  </button>
      <div className="lblocknav_container" ref={(elem) => (blockRef.current[0] = elem)}>
        <div className="lblocknav">
          <span className="mtitle">
            <a href="#main" className="col_b"> <b>    </b> </a>
            <button className="lblocknav_btn2" onClick={() => {
              document.getElementsByClassName('lblocknav_btn')[0].style.display = 'block';
              document.getElementsByClassName('lblocknav_container')[0].style.display = 'none';
            }}>  </button>
          </span>
          <div className="hyperlink">
            <details open>
              <summary>  </summary>
              <details open>
                <summary className="col_g">   </summary>
                <a href="#Elements " className="col_p"> Elements  </a>
                <a href="#Console " className="col_p"> Console  </a>
                <a href="#Sources " className="col_p"> Sources   </a>
                <a href="#Network " className="col_p"> Network   </a>
                <a href="#Performance " className="col_p"> Performance  </a>
                <a href="#Memory " className="col_p"> Memory   </a>
                <a href="#Application " className="col_p">Application  </a>
                <a href="#Security " className="col_p"> Security  </a>
                <a href="#Lighthouse " className="col_p"> Lighthouse  </a>
                {/* <a href="#" className="col_p"> </a> */}
              </details>
            </details>
          </div>
        </div>
      </div>
      <div className="common_style" ref={(elem) => (blockRef.current[1] = elem)} >
        <span className="lblock">
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">  Elements  <a name="Elements " style={{ visibility: "hidden" }}> Elements  </a> </summary>
              <span className="sblock">
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">  Console  <a name="Console " style={{ visibility: "hidden" }}> Console  </a> </summary>
              <span className="sblock">
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">  Sources  <a name="Sources " style={{ visibility: "hidden" }}> Sources  </a> </summary>
              <span className="sblock">
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">  Network  <a name="Network " style={{ visibility: "hidden" }}> Network  </a> </summary>
              <span className="sblock">
                <span className="sstitle"> Headers </span>
                <span className="mblock">
                  <details>
                    <summary className="sstitle">  General </summary>
                    <span className="sblock">
                      <li> Request URL :    </li>
                      <li> Request Method: [GET, POST] # </li>
                      <li> Status Code : [200,302]  </li>
                      <li> Remote Address : ?? </li>
                      <details>
                        <summary> Referrer Policy :        </summary>
                        <small> HTTP referer :       ,
                                 </small>
                        <span className="mblock">
                          <small> {'<meta name="referrer" content="" /> '} </small>
                          <span className="sstitle"> content="no-referrer" </span>
                          <li> refere   </li>
                          <span className="sstitle"> content="unsafe-url" </span>
                          <li>    </li>
                          <span className="sstitle"> content="origin" </span>
                          <li>       </li>
                          <span className="sstitle"> content="no-referrer-when-downgrade" </span>
                          <li>   https    </li>
                          <span className="sstitle"> content="strict-origin" </span>
                          <li>   https     </li>
                          <span className="sstitle"> content="same-origin" </span>
                          <li>        </li>
                          <span className="sstitle"> content="origin-when-cross-origin" </span>
                          <li>    ,      </li>
                          <span className="sstitle"> content="strict-origin-when-cross-origin" </span>
                          <li> https    ,    http
                                  </li>
                        </span>
                      </details>
                    </span>
                  </details>
                  {/*  */}
                  <details>
                    <summary className="sstitle">  Response Headers </summary>
                    <span className="sblock">
                      <li> ache-Control: no-cache, no-store, max-age=0, must-revalidate </li>
                      <li> Connection: keep-alive </li>
                      <li> Content-Length: 0 </li>
                      <li> Date: Thu, 12 Aug 2021 15:46:38 GMT # </li>
                      <li> Expires: 0 </li>
                      <li> Keep-Alive: timeout=60  </li>
                      <li> Location: http://localhost:8080/ </li>
                      <li> Pragma: no-cache </li>
                      <li> Set-Cookie: JSESSIONID=4D435E9D3AF6BCCAA188BE74B8D1167D; Path=/; HttpOnly </li>
                      <li> X-Content-Type-Options: nosniff </li>
                      <li> X-Frame-Options: DENY </li>
                      <li> X-XSS-Protection: 1; mode=block </li>
                    </span>
                  </details>
                  {/*  */}
                  <details>
                    <summary className="sstitle">  Request Headers </summary>
                    <span className="sblock">
                      <li> Accept: text/html,application/xhtml+xml,application/xml;q=0.9,image/avif,image/webp,image/apng,*/*;q=0.8,application/signed-exchange;v=b3;q=0.9 </li>
                      <li> Accept-Encoding: gzip, deflate, br </li>
                      <li> Accept-Language: ko,en-US;q=0.9,en;q=0.8 </li>
                      <li> Cache-Control: max-age=0 </li>
                      <li> Connection: keep-alive </li>
                      <li> Content-Length: 28 </li>
                      <li> Content-Type: application/x-www-form-urlencoded </li>
                      <li> Cookie: JSESSIONID=8853C84951CDCDE9026D41B7770FB10B </li>
                      <li> Host: localhost:8080 </li>
                      <li> Origin: http://localhost:8080 </li>
                      <li> Referer: http://localhost:8080/loginForm </li>
                      <li> Sec-Fetch-Dest: document </li>
                      <li> Sec-Fetch-Mode: navigate </li>
                      <li> Sec-Fetch-Site: same-origin </li>
                      <li> Sec-Fetch-User: ?1 </li>
                      <li> Sec-GPC: 1 </li>
                      <li> Upgrade-Insecure-Requests: 1 </li>
                      <li> User-Agent: Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/92.0.4515.131 Safari/537.36 </li>
                    </span>
                  </details>
                  {/*  */}
                  <details>
                    <summary className="sstitle">  Form Data </summary>
                    <span className="sblock">
                      <li>  </li>
                      <li>  </li>
                    </span>
                  </details>
                  {/*  */}
                </span>
              </span>
              {/*  */}
              <span className="sblock">
                <span className="sstitle"> Preview </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
              {/*  */}
              <span className="sblock">
                <span className="sstitle"> Response </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
              {/*  */}
              <span className="sblock">
                <span className="sstitle"> Initiator </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
              {/*  */}
              <span className="sblock">
                <span className="sstitle"> Timing </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
              {/*  */}
              <span className="sblock">
                <span className="sstitle"> Cookies </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
              {/*  */}
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">  Performance  <a name="Performance " style={{ visibility: "hidden" }}> Performance  </a> </summary>
              <span className="sblock">
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">  Memory  <a name="Memory " style={{ visibility: "hidden" }}> Memory  </a> </summary>
              <span className="sblock">
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">  Application  <a name="Application " style={{ visibility: "hidden" }}> Application  </a> </summary>
              <span className="sblock">
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">  Security  <a name="Security " style={{ visibility: "hidden" }}> Security  </a> </summary>
              <span className="sblock">
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">  Lighthouse  <a name="Lighthouse " style={{ visibility: "hidden" }}> Lighthouse  </a> </summary>
              <span className="sblock">
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
            </details>
          </span>
          {/*  */}
        </span>
      </div>
    </>
  );
}

export default BackendBrowserDevTool;