import React, { useEffect, useRef } from 'react';

const LinuxDirectory = (props) => {

  function logit() {

    // 
    let elementHeight1 = document.getElementsByClassName('navMenu')[0].scrollHeight;
    let elementHeight2 = document.getElementsByClassName('navMenu')[0].scrollHeight +
      document.getElementsByClassName('navMenu2')[0].scrollHeight + 26;
    let testdis = document.getElementsByClassName('common_style')[0].getBoundingClientRect().left
      - document.getElementsByClassName('lblocknav_container')[0].getBoundingClientRect().left;
    // console.log(window.outerHeight);


    //    +  
    if (window.innerWidth < 1024) {
      blockRef.current[0].style.position = 'fixed';
      document.documentElement.scrollTop < elementHeight1 ?
        blockRef.current[0].style.top = elementHeight1 + "px" :
        blockRef.current[0].style.top = "10px";
      blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
      blockRef.current[0].style.maxWidth = '420px';
      blockRef.current[1].style.left = '25%';
      blockRef.current[0].style.height = window.outerHeight;
      blockRef.current[0].style.display === 'inline-block' ?
        document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none' :
        document.getElementsByClassName('lblocknav_btn')[0].style.display = 'inline-block';
    }
    //   
    else if (document.documentElement.scrollTop < elementHeight2 && window.innerWidth > 1023) {
      //    ,  ,  
      blockRef.current[0].style.position = 'fixed';
      blockRef.current[0].style.top = elementHeight2 + "px";
      blockRef.current[0].style.display = 'inline-block';
      blockRef.current[0].style.maxWidth = testdis + "px";
      blockRef.current[0].style.height = window.outerHeight;
      blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
      //       
      blockRef.current[1].style.left = '25%';
      document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
    }
    else if (document.documentElement.scrollTop > elementHeight2 && window.innerWidth > 1023) {
      //    ,  ,  
      blockRef.current[0].style.position = 'fixed';
      blockRef.current[0].style.top = "10px";
      blockRef.current[0].style.display = 'inline-block';
      blockRef.current[0].style.maxWidth = testdis + "px";
      blockRef.current[0].style.height = window.outerHeight;
      blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
      //       
      blockRef.current[1].style.left = '25%';
      document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
    }
  }

  const blockRef = useRef([]);

  useEffect(() => {
    function watchScroll() {
      window.addEventListener("scroll", logit);
    }
    watchScroll();
    return () => {
      window.removeEventListener("scroll", logit);
    };
  });

  return (
    <>
      <button className="lblocknav_btn" onClick={() => {
        blockRef.current[0].style.display === 'inline-block' ?
          blockRef.current[0].style.display = 'none' :
          blockRef.current[0].style.display = 'inline-block';
        document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
      }}>  </button>
      <div className="lblocknav_container" ref={(elem) => (blockRef.current[0] = elem)}>
        <div className="lblocknav">
          <span className="mtitle">
            <a href="#main" className="col_b"> <b>    </b> </a>
            <button className="lblocknav_btn2" onClick={() => {
              document.getElementsByClassName('lblocknav_btn')[0].style.display = 'block';
              document.getElementsByClassName('lblocknav_container')[0].style.display = 'none';
            }}>  </button>
          </span>
          <div className="hyperlink">
            <details open>
              <summary>  </summary>
              <details open>
                <summary className="col_g">   </summary>
                <a href="#" className="col_p">  </a>
                {/* <a href="#" className="col_p"> </a> */}
              </details>
            </details>
          </div>
        </div>
      </div>
      <div className="common_style" ref={(elem) => (blockRef.current[1] = elem)} >
        <span className="lblock">
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">      <a name="" style={{ visibility: "hidden" }}> </a> </summary>
              <span className="sblock">
                {/*  */}
                <details>
                  <summary className="sstitle">    / </summary>
                  <span className="mblock">
                    <li>  ,  C  ,    </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  dev </summary>
                  <span className="mblock">
                    <li>     </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  home </summary>
                  <span className="mblock">
                    <li>     </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  media </summary>
                  <span className="mblock">
                    <li> CD, USB  ()   </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  opt </summary>
                  <span className="mblock">
                    <li>     </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  root - [] </summary>
                  <span className="mblock">
                    <li> root  , /   </li>
                    <li> .bashrc :     , alias   
                      <li> alias []='[] []' , [su ]     </li>
                    </li>
                    <small> .bashrc  alias       . </small>
                    <li> </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  sys </summary>
                  <span className="mblock">
                    <li>       </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  usr </summary>
                  <span className="mblock">
                    <li>     ,      . </li>
                    <li> /bin/sh </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  boot </summary>
                  <span className="mblock">
                    <li>      . </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  etc </summary>
                  <span className="mblock">
                    <li>       . </li>
                    {/*  */}
                    <li> /passwd :      
                      <span className="sblock">
                        <details>
                          <summary className="sstitle">   [ |  | UID | GID | comment |  | ] </summary>
                          <li>  :   </li>
                          <li>  : [ x:shadow | ]  </li>
                          <li> UID :  User ID </li>
                          <li> GID :  Group ID </li>
                          <li> comment :  (  ) </li>
                          <li>  :    </li>
                          <li>  :    (bash     )  </li>
                        </details>
                      </span>
                    </li>
                    {/*  */}
                    <li> /shadow :       
                      <span className="sblock">
                        <details>
                          <summary className="sstitle">  [ |   | ]  </summary>
                          <li>  :   </li>
                          <li>   : $algorithm_id$salt$encrypted_password
                            <span className="mblock">
                              <li> algorithm_id :   [1:MD5, 2:BlowFish, 5:SHA-256, 6:SHA-512 ] </li>
                              <li> salt :     </li>
                              <li> encrypted_password :
                                <li> * :       </li>
                                <li> ! :             </li>
                                <li> empty :      </li>
                              </li>
                              <li>  </li>
                            </span>
                          </li>
                          <li>     : 1970 1 1       </li>
                          <li>    :          </li>
                          <li>    :    </li>
                          <li>  :      </li>
                          <li>  :    ,     </li>
                          <li>  :   , 1970 1 1   </li>
                        </details>
                      </span>
                    </li>
                    {/*  */}
                    <li> /securetty :       
                      <span className="sblock">
                        <details>
                          <summary className="sstitle">  /bin/login       </summary>
                          <small>  </small>
                          <li> tty(TeleTYpewriter) :    </li>
                          <li> pts(Pseudo TTY Slave) :       </li>
                          <li> ttyS :        </li>
                        </details>
                      </span>
                    </li>
                    {/*  */}
                    <li> /ssh/sshd_config
                      <span className="sblock">
                        <details>
                          <summary className="sstitle">  ssh     </summary>
                          <li>  PermitRootLoing yes #root   </li>
                        </details>
                      </span>
                    </li>
                    {/*  */}
                    <li> /group
                      <span className="sblock">
                        <details>
                          <summary className="sstitle">       </summary>
                          <li> sudo groupadd  , #  </li>
                          <li> sudo groupdel  , #   </li>
                          <li> sudo gpasswd  , #  (    )  </li>
                          <li> sudo gpasswd -r  , #    </li>
                          <li> sudo gpasswd -A   , #     </li>
                          <li> sudo gpasswd -a   , #    </li>
                          <li> sudo gpasswd -d   , #     </li>
                          <li> sudo usermod -a -g   , #    </li>
                          <li> sudo usermod -a -G   , #   +     </li>
                          <li> newgrp  , #    </li>
                        </details>
                      </span>
                    </li>
                    {/*  */}
                    <li> /etc/default/useradd
                      <span className="sblock">
                        <details>
                          <summary className="sstitle">      </summary>
                          <li> GROUP :   GID , 100 </li>
                          <li> HOME :     , /home </li>
                          <li> INACTIVE :      , 1  </li>
                          <li> EXPIRE :    , 1 </li>
                          <li> SHELL :     , /bin/bash </li>
                          <li> SKEL :    (          ) , /etc/skel </li>
                        </details>
                      </span>
                    </li>
                    {/*  */}
                    <li> /etc/login.defs
                      <span className="sblock">
                        <details>
                          <summary className="sstitle">          </summary>
                          <li> MAIL_DIR :    , # /var/spool/mail  </li>
                          <li> PASS_MAX_DAYS :    , # 99999 </li>
                          <li> PASS_MIN_DAYS :    , # 0 </li>
                          <li> PASS_MIN_LEN	 :    , # 5 </li>
                          <li> PASS_WARN_AGE :    , # 7 </li>
                          <li> UID_MIN , UID_MAX :   UID , # 1000,60000 </li>
                          <li> SYS_UID_MIN , SYS_UID_MAX :   UID , # 210 , 999 </li>
                          <li> GID_MIN , GID_MAX :   GID , # 1000,60000 </li>
                          <li> SYS_GID_MIN , SYS_GID_MAX :   GID , # 210 , 999 </li>
                          <li> CREATE_HOME :     , # yes  </li>
                          <li> UMASK : umask  () , # 077 , =777-unmask </li>
                          <li> USERGROUPS_ENAB :       , # yes  </li>
                          <li> ENCRYPT_METHOD :   , # SHA512  </li>
                        </details>
                      </span>
                    </li>
                    {/*  */}
                    <li> /
                      <span className="sblock">
                        <details>
                          <summary className="sstitle">    </summary>
                          <li>   </li>
                          <li>   </li>
                        </details>
                      </span>
                    </li>
                    {/*  */}
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  lost+found </summary>
                  <span className="mblock">
                    <li>       </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  mnt </summary>
                  <span className="mblock">
                    <li>       </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  proc </summary>
                  <span className="mblock">
                    <li>         </li>
                    <li> ls -la /proc :     </li>
                    <li> ls -la /proc/PID : PID     </li>
                    <li> ls -la /proc/$$/fd :    </li>
                    <li> exec 0&lt;&amp;- : 0   ,    </li>
                    <li> exec 1&lt;&amp;- : 1   ,    </li>
                    <li> exec 2&lt;&amp;- : 2   ,    </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  run </summary>
                  <span className="mblock">
                    <li>       </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  srv </summary>
                  <span className="mblock">
                    <li> FTP Web      . </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  tmp </summary>
                  <span className="mblock">
                    <li>     ,     </li>
                  </span>
                </details>
                {/*  */}
                <details>
                  <summary className="sstitle">  var </summary>
                  <span className="mblock">
                    <li>          </li>
                  </span>
                </details>
                {/*  */}
              </span>
            </details>
          </span>
          {/*  */}
          <span className="mblock">
            <details>
              <summary className="stitle">  <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
              <span className="sblock">
                <span className="sstitle">  </span>
                <span className="mblock">
                  <li>  </li>
                </span>
              </span>
            </details>
          </span>
          {/*  */}
        </span>
      </div>
    </>
  );
}

export default LinuxDirectory;