import React, { useEffect, useRef } from 'react';

const LinuxUbuntu = (props) => {

    function logit() {

        // 
        let elementHeight1 = document.getElementsByClassName('navMenu')[0].scrollHeight;
        let elementHeight2 = document.getElementsByClassName('navMenu')[0].scrollHeight +
            document.getElementsByClassName('navMenu2')[0].scrollHeight + 26;
        let testdis = document.getElementsByClassName('common_style')[0].getBoundingClientRect().left
            - document.getElementsByClassName('lblocknav_container')[0].getBoundingClientRect().left;
        // console.log(window.outerHeight);


        //    +  
        if (window.innerWidth < 1024) {
            blockRef.current[0].style.position = 'fixed';
            document.documentElement.scrollTop < elementHeight1 ?
                blockRef.current[0].style.top = elementHeight1 + "px" :
                blockRef.current[0].style.top = "10px";
            blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
            blockRef.current[0].style.maxWidth = '420px';
            blockRef.current[1].style.left = '25%';
            blockRef.current[0].style.height = window.outerHeight;
            blockRef.current[0].style.display === 'inline-block' ?
                document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none' :
                document.getElementsByClassName('lblocknav_btn')[0].style.display = 'inline-block';
        }
        //   
        else if (document.documentElement.scrollTop < elementHeight2 && window.innerWidth > 1023) {
            //    ,  ,  
            blockRef.current[0].style.position = 'fixed';
            blockRef.current[0].style.top = elementHeight2 + "px";
            blockRef.current[0].style.display = 'inline-block';
            blockRef.current[0].style.maxWidth = testdis + "px";
            blockRef.current[0].style.height = window.outerHeight;
            blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
            //       
            blockRef.current[1].style.left = '25%';
            document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
        }
        else if (document.documentElement.scrollTop > elementHeight2 && window.innerWidth > 1023) {
            //    ,  ,  
            blockRef.current[0].style.position = 'fixed';
            blockRef.current[0].style.top = "10px";
            blockRef.current[0].style.display = 'inline-block';
            blockRef.current[0].style.maxWidth = testdis + "px";
            blockRef.current[0].style.height = window.outerHeight;
            blockRef.current[0].style.left = document.getElementsByClassName('navMenu2')[0].getBoundingClientRect().left + "px";
            //       
            blockRef.current[1].style.left = '25%';
            document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
        }
    }

    const blockRef = useRef([]);

    useEffect(() => {
        function watchScroll() {
            window.addEventListener("scroll", logit);
        }
        watchScroll();
        return () => {
            window.removeEventListener("scroll", logit);
        };
    });

    return (
        <>
            <button className="lblocknav_btn" onClick={() => {
                blockRef.current[0].style.display === 'inline-block' ?
                    blockRef.current[0].style.display = 'none' :
                    blockRef.current[0].style.display = 'inline-block';
                document.getElementsByClassName('lblocknav_btn')[0].style.display = 'none';
            }}>  </button>
            <div className="lblocknav_container" ref={(elem) => (blockRef.current[0] = elem)}>
                <div className="lblocknav">
                    <span className="mtitle">
                        <a href="#main" className="col_b"> <b>    </b> </a>
                        <button className="lblocknav_btn2" onClick={() => {
                            document.getElementsByClassName('lblocknav_btn')[0].style.display = 'block';
                            document.getElementsByClassName('lblocknav_container')[0].style.display = 'none';
                        }}>  </button>
                    </span>
                    <div className="hyperlink">
                        <details open>
                            <summary>  </summary>
                            <details open>
                                <summary className="col_g">   </summary>
                                <a href="#" className="col_p">  </a>
                                {/* <a href="#" className="col_p"> </a> */}
                            </details>
                        </details>
                    </div>
                </div>
            </div>
            <div className="common_style" ref={(elem) => (blockRef.current[1] = elem)} >
                <span className="lblock">
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  VMware Ubuntu   <a name="" style={{ visibility: "hidden" }}> </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li> <a href="https://ubuntu.com/download/desktop/thank-you?version=20.04.2.0&amp;architecture=amd64" target="_blank" rel="noreferrer">   </a> </li>
                                    <li> CentOS-7-x86_64-DVD-2009.iso  </li>
                                    <li> VMware Workstation  </li>
                                    <li> Create a New Virtual Machine </li>
                                    <li> Typical  </li>
                                    <li> I will install the operation system later  </li>
                                    <li> Linux , CentOS 7 64-bit  </li>
                                    <li> Virtual machine name: CentOS7_Server   (  ) </li>
                                    <li> 20GB , Split virtual disk into multiple files  </li>
                                    <li> Customize Hardware...  </li>
                                    <li> Memory :   2GB(2048MB)  </li>
                                    <li> New CD/DVD (IDE) : Use ISO image file:  CentOS7  </li>
                                    <li> Network Adapter : Custom  VMnet8(NAT)  </li>
                                    <li> Sound Card, Printer remove (  ) </li>
                                    <li> close finish </li>
                                    <li>   Ubuntu   </li>
                                    <li>   : Korean  101/104   </li>
                                    <li>     </li>
                                    <li>   Ubuntu  -   -  </li>
                                    <li>  Seoul </li>
                                    <li>  :  </li>
                                    <li>   : .com </li>
                                    <li>    : </li>
                                    <li>   : P@ssw0rd! </li>
                                    <li>  </li>
                                    <li>  -   -  -   -  -  </li>
                                    <li>  -   -  -  -  -  </li>
                                    <li>  -   -  -    </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  Ubuntu   <a name="Ubuntu  " style={{ visibility: "hidden" }}> Ubuntu   </a> </summary>
                            <span className="sblock">
                                {/*  */}
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li> sudo passwd root #   </li>
                                    <li> su - root #  </li>
                                    <li>  </li>
                                    <li>  </li>
                                </span>
                                {/*  */}
                                <span className="sstitle">   </span>
                                <span className="mblock">
                                    <li> ls -la /etc/netplan :        </li>
                                    <li> cd /etc/netplan  - vi 01-network-manager-all.yaml </li>
                                    <li> ethernets:  </li>
                                    <li> &nbsp;ens33:  </li>
                                    <li> &nbsp;&nbsp;addresses: [192.168.10.90/24] </li>
                                    <li> &nbsp;&nbsp;gateway4: 192.168.10.2 </li>
                                    <li> &nbsp;&nbsp;nameservers: </li>
                                    <li> &nbsp;&nbsp;&nbsp;addresses: [168.126.63.1,8.8.8.8] </li>
                                    <li> &nbsp;&nbsp;dhcp4: no </li>
                                    <li> cat 01-network-manager-all.yaml   </li>
                                    <li> netplan apply #netplan  ?</li>
                                    <li> ip route # ip      </li>
                                    <li>   winmerge    </li>
                                    <li>  </li>
                                </span>
                                {/*  */}
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                                {/*  */}
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  telnet    <a name="telnet   " style={{ visibility: "hidden" }}> telnet    </a> </summary>
                            <span className="sblock">
                                <small> :    ,    SSH  </small>
                                <span className="sstitle"> 1 </span>
                                <span className="mblock">
                                    <li> 0. sudo passwd root </li>
                                    <li> 0. su - #     </li>
                                    <li> 1. apt-get install xinetd </li>
                                    <li> 2. apt-get install telnetd </li>
                                    <li> 3. vi /etc/xinetd.conf
                                        <li> {'service telnet '} </li>
                                        <li> {'{ '} </li>
                                        <li> {'        disable = no '} #   no  </li>
                                        <li> {'        flags = REUSE '} # </li>
                                        <li> {'        socket_type = stream '} # </li>
                                        <li> {'        wait = no '} #  ?</li>
                                        <li> {'        user = root '} #  </li>
                                        <li> {'        server = /usr/sbin/in.telnetd '} #   </li>
                                        <li> {'        log_on_failure += USERID '} #    </li>
                                        <li> {'} '} </li>
                                    </li>
                                    <li> /etc/init.d/xinetd restart </li>
                                    <li> apt-get install net-tools # netstat   </li>
                                    <li> netstat -tnlp #tcp   ? ,  </li>
                                    <li> ufw enable # ? </li>
                                    <li> ufw statue #    </li>
                                    <li> ufw allow 23/tcp #tcp23   </li>
                                    <li> vi /etcpam.d/login  10   auth required pam_securetty.so   </li>
                                    <li> systemctl restart xinetd </li>
                                    <li> vi etc/securetty </li>
                                    <li>  </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">    <a name=" " style={{ visibility: "hidden" }}>   </a> </summary>
                            <span className="sblock">
                                <span className="sstitle"> csh  </span>
                                <span className="mblock">
                                    <li>   C  </li>
                                    <li> apt-get install csh </li>
                                    <li>  </li>
                                </span>
                                {/*  */}
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                                {/*  */}
                            </span>
                        </details>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <details>
                            <summary className="stitle">  <a name="" style={{ visibility: "hidden" }}>  </a> </summary>
                            <span className="sblock">
                                <span className="sstitle">  </span>
                                <span className="mblock">
                                    <li>  </li>
                                </span>
                            </span>
                        </details>
                    </span>
                    {/*  */}
                </span>
            </div>
        </>
    );
}

export default LinuxUbuntu;