import React from 'react';
import './LsideMenuStyle.css';

const BlankList = (props) => {
    function send(page2) {
        props.onChangePage(page2)
    }

    return (
        <div className="menucontainer">
            <div className="menu menu1">
                <button onClick={() => { send('securitystudy') }}>   </button>
                <button onClick={() => { send('jwt') }}> JWT  </button>
                <button onClick={() => { send('hmac') }}> HMAC  </button>
                <button onClick={() => { send('hash') }}> HASH </button>
                <button onClick={() => { send('encodingdecoding') }}>   </button>
                <button onClick={() => { send('hackingattack') }}>   </button>
                <button onClick={() => { send('ipspoofing') }}> IP Spoofing </button>
            </div>
            <div className="menu menu2">
                <button onClick={() => { send('') }}>  </button>
            </div>
        </div>
    );
}

export default BlankList;